import axios from 'axios'
import React from 'react'

const DEleteClass = (props) => {
    const deleteClass = ()=>{
        axios.post('/class/deletclass',{
            className: props.module
        }).then((res)=>{
            console.log(res);
        })
    }
  return (
    <div className='leaveClass' onClick={deleteClass}>
        <h5>Delete Class</h5>
    </div>
  )
}

export default DEleteClass