import React,{useState,useEffect} from 'react'
import Course from './GlobalSubCompenents/Course'
import profprofile from '../assets/profprofile.png'
import './styles/learning/learningstyle.css'



import AddCourse from './corsesSubCompenents/AddCourse'
import axios from 'axios'
import { useParams } from 'react-router-dom'
import DEleteClass from './corsesSubCompenents/DEleteClass'


const LearningTeacher = (props) => {
  const module = useParams().courseName
  const [courseList, setcourseList] = useState({result:[]})
  const getAllCourses = ()=>{
    axios.get("/class/allmodulecourses",{
      params:{
        className: module
      }
    }).then((res)=>{
      setcourseList({result:res.data.result,info:res.data.info})
    })
  }
  useEffect(() => {
    getAllCourses()
  }, [])
  
    return (
      <div className='whole-pg'>
        <div className='flexit'>
          <h5 className='heading'>{props.module}</h5>
          <DEleteClass module={module}/>
        </div>
        <AddCourse/>
        <div className='postes'>
        {
          courseList.result[0] && (
            courseList.result.map((course)=>{
              return <Course profile={profprofile} name={courseList.info.firstName+" "+courseList.info.lastName} module={props.module} title={course.pdfName} text={course.content}/>

            })
          )
        }
        </div>
        
      </div>
    )
}

export default LearningTeacher