import React,{useRef, useState} from 'react'
import postArrow from '../../assets/postArrow.svg'
import bluePostArrow from '../../assets/bluePostArrow.svg'
import plus from '../../assets/+.svg'
import axios from 'axios'
import { useParams } from 'react-router-dom'


const AddCourse = (props) => {
  axios.defaults.withCredentials = true
  const [file, setFile] = useState();
  const content = useRef("")
  const saveFile = (e) => {

        setFile(e.target.files[0]);

  };
  const module = useParams().courseName
  const postCourse = ()=>{
    const formData = new FormData();
    formData.append("file", file);
    formData.append("content", content.current.value);
    formData.append("className", module);
    try {

    axios.post(`/class/uploadcourse`,formData,{headers: {
      "Content-Type": "multipart/form-data",
      }},).then((res)=>{
      if(res.data.succes){
        content.current.value=""
        setFile(null)
        }
        })

        } catch (ex) {

          console.log(ex);

        }
  }

  return (
    <div className='addCourse'>
    <h5 onClick={postCourse}>Post </h5>
        <div>
            <textarea placeholder='Type something' ref={content}></textarea>
        </div>
        <div>
            <img src={plus} />
            <input type='file' placeholder='Attach file' onChange={(e)=>{saveFile(e)}} />
            <p>Attach file</p>
        </div>
    </div>
  )
}

export default AddCourse